body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.main-post{
  position: relative;
}

.main-post div{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff !important;
  background: #4c4c4c7a;
  cursor: pointer;
}

.card{
  cursor: pointer;
}

.card-profile{
  padding: 20px;
}

.card-profile .image-profile{
  border-radius: 50%;
}

.sidebar{
  display: flex;
  align-items: center;
  flex-direction: column;
}

.card-img{
  width: 100% !important;
  height: 100% !important;
  object-fit: cover !important;
}

.post-image{
  height: 70vh;
  object-fit: cover;
}

#content::first-letter{
  font-size: 38px;
  font-size: bold;
}